import React from 'react';
import { useState, useEffect  } from 'react';
import { UserList, UserItem, UserButton, PageContainer, UserName, UserInfos, UserInfo } from './UserListPageStyles';
import axios from "../../api/axios";
import  {useNavigate  } from "react-router-dom";
import { useCookies } from 'react-cookie';

const UserListPage = () => {
	const [cookies, setCookie, removeCookie] = useCookies(['login']);

    const [users, setUsers] = useState([]);
    const navigate = useNavigate();

    useEffect(() => {  
        console.log("cookie", cookies)
        fetchData();
    }, [])

    const fetchData = async () => {
        const request = await axios.get(`/members`);
        console.log('request', request.data);
        setUsers(request.data.memberItems);
    };

    const handleCreateOrderClick = (user, e) => {
        navigate(`../userdetail/${user.id}`   )
    }

    return (
        <PageContainer>
            <UserList>
                {users.map((user, index) => (
                    <UserItem key={index}>
                        <UserInfos>
                            <UserName>{user.name}</UserName>
                            <UserInfo>: {user.email}</UserInfo>
                            <UserInfo>: {user.mobile}</UserInfo>
                            <UserInfo>: {user.organization}</UserInfo>
                            <UserInfo>: {user.department}</UserInfo>
                            <UserInfo>: {user.position}</UserInfo>
                            <UserInfo>: {user.createdDatetime}</UserInfo>
                        </UserInfos>
                        <UserButton  onClick={(e) => {handleCreateOrderClick(user, e)}} > </UserButton>
                    </UserItem>
                ))}
            </UserList>
        </PageContainer>
    );
};

export default UserListPage;
