import React, { useState, useEffect } from 'react';
import {
    Container,
    Header,
    SubHeader,
    Information,
    OrderList,
    OrderItem,
    GoButton,
} from './AdminUserDetailPageStyle';
import axios from "../../api/axios";
import { useCookies } from 'react-cookie';

import  {useNavigate, useLoaderData, } from "react-router-dom";

export async function loader({ params }) {
    const userId = params.userId
    return { userId };
  }

const AdminUserDetailPage = () => {
    const { userId } = useLoaderData();
    const [userData, setUserData] = useState(null);
    const navigate = useNavigate();
	const [cookies, setCookie, removeCookie] = useCookies(['login']);

    useEffect(() => {
        console.log("cookie", cookies)
        fetchData();
    }, [userId]);

    const handelGoToThread = (e, threadID, userId) => {
        navigate(`../thread/${threadID}/${userId}`)
    }

    const handelGoToDetailedInformation = (e, orderNumber) => {
        navigate(`../orders/${orderNumber}/detail`)
    }

    const fetchData = async () => {
        const request = await axios.get(`/members/${userId}/detail`);
        console.log('request', request.data);
        setUserData(request.data)
    };

    if (!userData) {
        return <div>Loading...</div>;
    }

    return (
        <Container>
            <Header> </Header>
            <SubHeader>: {userData.name}</SubHeader>
            <Information>: {userData.email}</Information>
            <Information>: {userData.mobile}</Information>
            <Information>: {userData.organization}</Information>
            <Information>: {userData.department}</Information>
            <Information>: {userData.position}</Information>
            <Information>: {userData.createdDatetime}</Information>
            
            <SubHeader> </SubHeader>
            <Information> {userData.adminMemo ? userData.adminMemo : "N/A"}</Information>
            
            
            <SubHeader> </SubHeader>
            <OrderList>
                {userData.orderHistory.map(order => (
                    <OrderItem key={order.id} style={{position:'relative'}}>
                        <div style={{display:'inline-block'}}>
                            <Information style={{fontWeight: "bold"}}>: {order.orderNumber}</Information>
                            <Information> : {order.items}</Information>
                            <Information>: {order.status}</Information>
                            <Information>: {order.price}</Information>
                            <Information>: {order.createdDatetime}</Information>
                            <Information>: {order.updatedDatetime}</Information>
                        </div>
                        <GoButton style={{position:'absolute', width: '30%', right: '5%', bottom: '10%', display:'inline-block'}} onClick={(e) => {handelGoToDetailedInformation(e, order.orderNumber )}}>  </GoButton>
                    </OrderItem>
                ))}
            </OrderList>
            <SubHeader>    </SubHeader>
                       .
            <OrderList>
                {userData.threads.map(order => (
                    <OrderItem key={order.id} style={{position:'relative'}}>
                        <div style={{display:'inline-block'}}>
                            <Information> Id: {order.id}</Information>
                            <Information>: {order.category}</Information>
                            <Information>: {order.createdDatetime}</Information>
                        </div>
                        <GoButton style={{position:'absolute', width: '30%', right: '5%', bottom: '10%'}} onClick={(e) => {handelGoToThread(e, order.id, userData.id)}}> </GoButton>
                    </OrderItem>
                ))}
            </OrderList>
        </Container>
    );
};

export default AdminUserDetailPage;
